@font-face {
  font-family: cheese;
  src: url(/chinese-cheese/build/static/media/cheese.1ee9941f.ttf);
}
#Dashboard {
  position: absolute;
  z-index: 9999;
  width: 320px;
  height: 200px;
  top: 400px;
  left: 50%;
  margin-left: -800px;
  background-color: #e0be88;
  border-radius: 30px;
  padding: 30px 25px 40px;
  box-sizing: border-box;
}
.dashboard_text {
  font-size: 28px;
  text-align: center;
  line-height: 40px;
  margin: 0px;
  font-family: cheese;
}
.dashboard_btns {
  display: inline-block;
  width: 270px;
}
.dashboard_btn {
  float: left;
  font-size: 28px;
  text-align: center;
  border: 1px solid #916722;
  border-radius: 5px;
  line-height: 40px;
  margin: 0px;
  font-family: cheese;
  display: inline-block;
  width: 120px;
  height: 40px;
  margin: 15px 30px 15px 0px;
  cursor: pointer;
  font-weight: bold;
  -webkit-user-select: none;
     -moz-user-select: none;
      -ms-user-select: none;
          user-select: none;
  box-sizing: border-box;
}
.dashboard_btn:hover {
  color: rgba(0, 0, 0, 0.4);
}
.dashboard_btn:last-child {
  margin-right: 0px;
}
